'use client'

import { useEffect, useMemo, useState } from 'react'
import Link from 'next/link'
import {
  Trophy, Zap, Target, Globe, Briefcase, ShoppingBag, Shield, Swords, Home, Users, Dumbbell
} from 'lucide-react'
import { createClient } from '@/lib/supabase/client'
import { useCharacterStore } from '@/lib/character-store'

// ===== Typy =====
type City = { name: string; country: string }
type Character = {
  money: number
  level: number
  energy: number
  maxEnergy: number
  reputation: number
  city?: City | null
}

// Formatery
const currencyFmt = new Intl.NumberFormat('en-US', {
  style: 'currency',
  currency: 'USD',
  maximumFractionDigits: 0,
})

const numberFmt = new Intl.NumberFormat('en-US')

// ===== Reu≈ºywalne karty =====
function StatCard({
  label,
  value,
  className = '',
}: {
  label: string
  value: string
  className?: string
}) {
  return (
    <div className="ls-section">
      <div className="ls-section-content text-center">
        <p className={`text-3xl font-bold mb-1 ${className}`}>{value}</p>
        <p className="text-xs text-[#888] uppercase">{label}</p>
      </div>
    </div>
  )
}

function QuickActionCard({
  href,
  icon: Icon,
  label,
  hoverBorder = '#333',
  iconClass = '',
  ariaLabel,
}: {
  href: string
  icon: React.ComponentType<React.SVGProps<SVGSVGElement>>
  label: string
  hoverBorder?: string
  iconClass?: string
  ariaLabel?: string
}) {
  return (
    <Link href={href} className="group" aria-label={ariaLabel ?? label} prefetch>
      <div
        className="p-4 bg-[#1a1a1a] border border-[#333] transition-all rounded
                   hover:scale-105 focus-within:scale-105 outline-none
                   focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-[#5cb85c]
                   focus-visible:ring-offset-[#0c0c0c]"
        style={{ boxShadow: '0 0 0 0 rgba(0,0,0,0)' }}
      >
        <Icon className={`w-8 h-8 mx-auto mb-2 transition-transform group-hover:scale-110 ${iconClass}`} />
        <div className="text-xs font-bold text-[#fff] text-center">{label}</div>
      </div>
    </Link>
  )
}

export default function DashboardPage() {
  // Precyzyjne selektory = mniej rerender√≥w
  const character = useCharacterStore((s) => s.character) as Character | null
  const isLoading = useCharacterStore((s) => s.isLoading)
  const [username, setUsername] = useState<string>('Player')
  const [authLoading, setAuthLoading] = useState<boolean>(true)
  const [authError, setAuthError] = useState<string | null>(null)

  const supabase = useMemo(() => createClient(), [])

  // Wczytanie username z Supabase z bezpiecznym odmontowaniem
  useEffect(() => {
    let isActive = true
    ;(async () => {
      setAuthLoading(true)
      setAuthError(null)
      try {
        const { data, error } = await supabase.auth.getUser()
        if (error) throw error
        const user = data?.user
        if (!user) {
          if (isActive) setUsername('Player')
          return
        }
        const metaName = (user.user_metadata as any)?.username as string | undefined
        const fallbackFromEmail = user.email?.split('@')[0]
        if (isActive) setUsername(metaName || fallbackFromEmail || 'Player')
      } catch (e: any) {
        if (isActive) {
          setAuthError('Could not load profile')
          setUsername('Player')
        }
      } finally {
        if (isActive) setAuthLoading(false)
      }
    })()
    return () => {
      isActive = false
    }
  }, [supabase])

  // Lista akcji ‚Äì jedna deklaracja, zero duplikacji
  const actions = useMemo(
    () => [
      { href: '/dashboard/crimes', icon: Target, label: 'Crime', hoverBorder: 'var(--danger)', iconClass: 'text-danger' },
      { href: '/dashboard/gym', icon: Dumbbell, label: 'Gym', hoverBorder: 'var(--warning)', iconClass: 'text-warning' },
      { href: '/dashboard/jobs', icon: Briefcase, label: 'Jobs', hoverBorder: 'var(--success)', iconClass: 'text-success' },
      { href: '/dashboard/travel', icon: Globe, label: 'Travel', hoverBorder: 'var(--info)', iconClass: 'text-info' },
      { href: '/dashboard/city', icon: Home, label: 'City', hoverBorder: '#888', iconClass: 'text-[#888]' },
      { href: '/dashboard/market', icon: ShoppingBag, label: 'Market', hoverBorder: '#f0ad4e', iconClass: 'text-[#f0ad4e]' },
      { href: '/dashboard/combat', icon: Swords, label: 'Combat', hoverBorder: 'var(--danger)', iconClass: 'text-danger' },
      { href: '/dashboard/gang', icon: Shield, label: 'Gang', hoverBorder: '#9b59b6', iconClass: 'text-[#9b59b6]' },
    ],
    []
  )

  // Wsp√≥lny loader
  if (isLoading || !character) {
    return (
      <div className="flex items-center justify-center h-screen">
        <div className="text-center">
          {/* prosty skeleton/loader */}
          <div className="inline-block h-8 w-8 animate-spin rounded-full border-2 border-solid border-[#5cb85c] border-r-transparent mb-4"></div>
          <p className="text-[#888] text-xs uppercase">Loading...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-6 max-w-7xl mx-auto fade-in">
      {/* Header */}
      <div className="ls-section">
        <div className="ls-section-content">
          <h1 className="text-3xl font-bold text-[#fff] mb-1">
            Welcome back, {authLoading ? '...' : username}! üëã
          </h1>
          {!!authError && <p className="text-xs text-danger mb-1">{authError}</p>}
          <p className="text-sm text-[#888]">
            {character.city
              ? `You're currently in ${character.city.name}, ${character.city.country}`
              : 'Select a city to begin your journey'}
          </p>
        </div>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <StatCard label="Cash" value={currencyFmt.format(character.money)} className="text-success" />
        <StatCard label="Level" value={numberFmt.format(character.level)} className="text-info" />
        <StatCard
          label="Energy"
          value={`${numberFmt.format(character.energy)}/${numberFmt.format(character.maxEnergy)}`}
          className="text-warning"
        />
        <StatCard label="Reputation" value={numberFmt.format(character.reputation)} className="text-[#d0d0d0]" />
      </div>

      {/* Quick Actions */}
      <div className="ls-section">
        <div className="ls-section-header">Quick Actions</div>
        <div className="ls-section-content">
          <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-8 gap-3">
            {actions.map((a) => (
              <QuickActionCard
                key={a.href}
                href={a.href}
                icon={a.icon}
                label={a.label}
                hoverBorder={a.hoverBorder}
                iconClass={a.iconClass}
                ariaLabel={`Open ${a.label}`}
              />
            ))}
          </div>
        </div>
      </div>

      {/* Top Players & Achievements */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="ls-section">
          <div className="ls-section-header">
            <Trophy className="w-4 h-4 inline mr-2" />
            Top Players
          </div>
          <div className="ls-section-content">
            <p className="text-xs text-[#888] text-center py-8">üèÜ Leaderboard coming soon!</p>
            <Link href="/dashboard/leaderboard" className="ls-btn ls-btn-primary w-full text-center" prefetch>
              View Leaderboard
            </Link>
          </div>
        </div>

        <div className="ls-section">
          <div className="ls-section-header">
            <Users className="w-4 h-4 inline mr-2" />
            Online Players
          </div>
          <div className="ls-section-content">
            <p className="text-xs text-[#888] text-center py-8">üü¢ See who&apos;s online!</p>
            <Link href="/dashboard/players" className="ls-btn ls-btn-secondary w-full text-center" prefetch>
              Browse Players
            </Link>
          </div>
        </div>
      </div>

      {/* Daily Challenges */}
      <div className="ls-section">
        <div className="ls-section-header">
          <Zap className="w-4 h-4 inline mr-2" />
          Daily Challenges
        </div>
        <div className="ls-section-content">
          <div className="text-center py-12">
            <div className="text-6xl mb-4">üéØ</div>
            <p className="text-sm text-[#888] mb-4">Complete daily missions for exclusive rewards!</p>
            <div className="inline-flex items-center gap-2 px-4 py-2 bg-[#1a1a1a] border border-[#333] rounded">
              <span className="text-xs text-[#888]">Coming soon</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
